<template>
  <div>
    <b-row>
      <b-col>
        <h2>TMS Project</h2>
      </b-col>
    </b-row>

    <b-carousel id="carousel"
                style="text-shadow: 1px 1px 2px #000;"
                controls
                indicators
                background="#ababab"
                :interval="4000"
                v-model="slide"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd">

      <b-carousel-slide v-for="image in tmsImages" :key="image.id"
                        :text="image.description"
                        :img-src="image.src">
      </b-carousel-slide>
    </b-carousel>

    <b-row class="mt-5">
      <b-col>
        <h4>작업 내용</h4>
        <hr>
        <p>
          - Mobile Application API 작업<br>
          - portal page Back-end, Front-end 작업 (3PLCarrier, logosCarrier, brokerage)<br>
          - setting page Back-end, Front-end 작업 (landmark)
        </p>
      </b-col>
    </b-row>

    <b-row class="mt-2">
      <b-col>
        <h4>사용 기술</h4>
        <hr>
        <p>
          - HTML5<br>
          - CSS3<br>
          - Javascript<br>
          - JSP<br>
          - Spring<br>
          - MSSQL<br>
        </p>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'

  const {mapState} = createNamespacedHelpers('work');

  export default {
    name: "TMS",
    data() {
      return {
        slide: 0,
        sliding: null
      }
    },
    created() {
      this.passTheStatus('tms');
    },
    mounted() {
      this.$store.dispatch('work/getTMS')
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      }
    },
    computed: {
      ...mapState(['tmsImages'])
    }
  }
</script>

<style scoped>
  h2 {
    color: #e67e22;
  }

  p {
    color: #bdc3c7;
  }
</style>